.form {
  display: flex;
  flex-direction: column;
  max-width: 400px;
  margin-bottom: 50px;
  background-color: white;
  border-radius: 10px;
  padding: 20px;
    box-shadow: 4px 4px 8px 0 rgba(34,60,80,.2);
    margin-left: auto;
    margin-right: auto;
}
.label {
  display: flex;
  flex-direction: column;
  &:not(:last-child) {
    margin-bottom: 20px;
  }
}
.input {
    padding: 5px;
}
.btn{
    border: none;
    border-radius: 10px;
    background-color: orange;
    color: white;
    padding: 10px;
    transition: 250ms all ease;
  box-shadow: 4px 4px 8px 0 rgba(34,60,80,.2);


    &:hover,&:focus{
        background: rgb(165, 108, 1);
    }
}